.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: rgb(255, 0, 234);
  height: 1920px;
  padding: 20px;
  color: white;
  -webkit-animation: headerShit infinite 5s;
          animation: headerShit infinite 5s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;  
}

.App-title {
  font-size: 1.5em;
  -webkit-animation: titleShit infinite 1s;
          animation: titleShit infinite 1s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes titleShit {
  from { font-size: 1.5em; color: rgb(255, 0, 0); }
  to { font-size: 5em; color: rgb(0, 255, 157); }
}

@keyframes titleShit {
  from { font-size: 1.5em; color: rgb(255, 0, 0); }
  to { font-size: 5em; color: rgb(0, 255, 157); }
}

@-webkit-keyframes headerShit {
  from {background-color: rgb(255, 0, 234); }
  to {background-color: rgb(0, 204, 255); }
}

@keyframes headerShit {
  from {background-color: rgb(255, 0, 234); }
  to {background-color: rgb(0, 204, 255); }
}


